'use strict';

const slider = document.querySelector('.numbers');
const backgroundList = document.querySelector('.image-wrapper__photo');
const up = document.querySelector('.slider-button--up');
const down = document.querySelector('.slider-button--down');

up.disabled = true;

down.addEventListener('click', function() {
  move(1, '+');
});

up.addEventListener('click', function() {
  move(-1, '-');
});

let position = 0;

function move(shift, direction) {
  const count = slider.children.length;

  position += shift;

  down.disabled = position >= count - 1;
  up.disabled = position <= 0;

  position = (position + count) % count;

  backgroundList.style.transform = `translateY(${-position * (100 / 3)}%)`;
  slider.children[position].classList.toggle('numbers__item--first');

  if (direction === '+') {
    slider.children[position - 1].classList.toggle('numbers__item--first');
  } else {
    slider.children[position + 1].classList.toggle('numbers__item--first');
  }
}
